import React, { useState, useRef } from 'react';
import { connect } from 'react-redux';
import { useHistory } from 'react-router-dom';
import { updateQuery } from '../../redux/searchQuery/action';
import Cart from './../cart/index';


const UserPanel = ({ cartOpen, handleToggleCartShow, cartProducts, updateQuery }) => {

    const [open, setOpen] = useState(false);

    const searchInput = useRef();
    let history = useHistory();
    
    const handleOpen = () => {
        if (window.location.pathname !== '/products') {
            history.push('/products')
        } else {
            setOpen(!open);
            searchInput.current.focus();
        }
    }

    const handleChange = (e) => {
        updateQuery(e.target.value)
    }

    return (
        <div className="user-panel">
            <Cart classes={{ cart: cartOpen ? 'cart-open' : 'cart-closed' }} />
            <div className="user-panel-items">
                <ul className="user-panel-icons d-flex justify-content-between">
                    <li className="user-panel-icon" >
                        <i
                            onClick={handleToggleCartShow}
                            className="fas fa-shopping-cart"
                            aria-hidden="true">
                        </i>
                        <span className="cart-products-num text-primary">{cartProducts.length}</span>
                    </li>
                    <li className="user-panel-icon">
                        <i
                            className="fas fa-search" aria-hidden="true"
                            onClick={handleOpen}
                        ></i>
                        <input
                            id="search"
                            className={`search ${open ? ' open' : ''}`}
                            name="search"
                            placeholder="Search Products"
                            type="text"
                            ref={searchInput}
                            onChange={handleChange}
                        />
                    </li>
                    <li className="user-panel-icon">
                        <i className="fas fa-user" aria-hidden="true"></i>
                    </li>
                </ul>
            </div>
        </div>
    );
}

export default connect(null, { updateQuery })(UserPanel);